package com.ragnorak.cmparticle.ui.projectsScreen.viewmodel

import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import com.ragnorak.cmparticle.data.local.Project
import com.ragnorak.cmparticle.domain.usecases.GetProjectsUseCase
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch

class ProjectScreenViewModel (getProjectsUseCase: GetProjectsUseCase) : ViewModel() {

    private val _uiState = MutableStateFlow<List<Project>>(emptyList())
    val uiState = _uiState.asStateFlow()

    init {
        viewModelScope.launch{
            _uiState.value = getProjectsUseCase()
        }
    }
}