package com.ragnorak.cmparticle.ui.components

import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.size
import androidx.compose.material.Icon
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.dp
import androidx.compose.ui.unit.sp
import org.jetbrains.compose.resources.DrawableResource
import org.jetbrains.compose.resources.painterResource

@Composable
fun SetIcon(
    iconPainter: DrawableResource,
    name: String,
    colorOriginal: Color,
    colorClick: Color,
    darkTheme: Boolean
) {
    Box(
        contentAlignment = Alignment.Center
    ) {
        Column(horizontalAlignment = Alignment.CenterHorizontally) {
            Icon(
                modifier = Modifier.weight(1f),
                painter = painterResource(iconPainter),
                contentDescription = null,
                tint = if (darkTheme) colorOriginal else colorClick
            )
            Spacer(modifier = Modifier.size(2.dp))
            Text(
                text = name, fontSize = 16.sp,
                color = if (darkTheme) colorOriginal else colorClick,
                fontWeight = FontWeight.Bold
            )
        }
    }
}