package com.ragnorak.cmparticle.ui.mainScreen.viewmodel

import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import com.ragnorak.cmparticle.domain.usecases.GetLinkProviderUseCase
import com.ragnorak.cmparticle.getDeviceType
import com.ragnorak.cmparticle.ui.mainScreen.model.MainScreenState
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch

class MainScreenViewModel(getLinkProviderUseCase: GetLinkProviderUseCase) : ViewModel() {

    private val device = getDeviceType()
    private val _uiState = MutableStateFlow(MainScreenState())
    val uiState = _uiState.asStateFlow()

    init {
        viewModelScope.launch {
            _uiState.update { state ->
                val items = getLinkProviderUseCase()
                val isDesktop = device
                state.copy(
                    items = items,
                    isDesktop = isDesktop,
                    isLoading = false
                )
            }
        }
    }
}
